<template>
  <header>
    <nav class="bg-gray-800">
      <div class="mx-auto max-w-7xl px-2 sm:px-6 lg:px-8">
        <div class="relative flex h-16 items-center justify-between">
          <div class="absolute inset-y-0 left-0 flex items-center sm:hidden">
            <!-- Mobile menu button-->
            <button  
              @click="toggleMenu" 
              type="button" 
              class="relative inline-flex items-center justify-center rounded-md p-2 text-gray-400 hover:bg-gray-700 hover:text-white focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white" 
              aria-controls="mobile-menu" 
              aria-expanded="false">
              <span class="absolute -inset-0.5"></span>
              <!--
                Icon when menu is closed.
                Menu open: "hidden", Menu closed: "block"
              -->
              <svg class="block h-6 w-6" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" aria-hidden="true">
                <path stroke-linecap="round" stroke-linejoin="round" d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5" />
              </svg>
              <!--
                Icon when menu is open.
                Menu open: "block", Menu closed: "hidden"
              -->
              <svg class="hidden h-6 w-6" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" aria-hidden="true">
                <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
          <div class="flex flex-1 items-center justify-center sm:items-stretch sm:justify-start">
            <div class="hidden sm:ml-6 sm:block">
              <ul 
                class="flex space-x-4 navigation"
              >
                <li 
                  v-for="(page) in pages"
                  :key="page.id"
                  class="rounded-md px-3 py-2 text-sm font-medium"
                  @click="handlePageChange(page.id)"
                  :class="{
                    'bg-gray-900 text-white': (page.id === activePage),
                    'text-gray-300 hover:bg-gray-700 hover:text-white': (page.id !== activePage) 
                  }"
                 >{{ page.label }}</li>
              </ul>
            </div>
          </div>

        </div>
      </div>

  <!-- Mobile menu, show/hide based on menu state. -->
  <div v-if="menuIsOpen" class="sm:hidden" id="mobile-menu">

    <ul 
      class="space-y-1 px-2 pb-3 pt-2 navigation"
    >
      <li 
        v-for="(page) in pages"
        :key="page.id"
        class="rounded-md px-3 py-2 text-sm font-medium block"
        @click="handlePageChange(page.id)"
        :class="{
          'bg-gray-900 text-white': (page.id === activePage),
          'text-gray-300 hover:bg-gray-700 hover:text-white': (page.id !== activePage) 
        }"
       >{{ page.label }}</li>
    </ul>

  </div>
</nav>

  </header>
</template>

<script>
export default {
  name: "HeaderQuiz",
  props: {
    activePage: {
      type: String,
      default: null,
    },
    pages: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      menuIsOpen: false,
    };
  },
  methods:{
    toggleMenu() {
      this.menuIsOpen = !this.menuIsOpen
    },
    handlePageChange(nextPage) {
      this.$emit("handlePageChange", nextPage);
      if (this.menuIsOpen) this.menuIsOpen = false;
    }
  }
};
</script>

<style  scoped>
  .navigation li {
    cursor: pointer;
  }

</style>
